<template>
  <!--<v-container fluid grid-list-sm secondary>-->
  <v-card>

    <template>
      <v-parallax src="https://www.highreshdwallpapers.com/wp-content/uploads/2015/01/Glorious-Autumn-Sunshine-1920x1080.jpg" height="900">
        <v-layout column align-center justify-center>
          <h1 class="white--text">Vuetify.js</h1>
          <h4 class="white--text">Build your application today!</h4>
        </v-layout>
      </v-parallax>
    </template>


    <v-container fluid grid-list-md text-xs-center>

      <v-layout row wrap>
        <v-flex d-flex xs12 order-xs5>
          <v-layout column>
            <v-flex d-flex>
              <v-card color="blue-grey" dark tile flat>
                <v-card-text>
                  Biru >> Biru >> Biru >> Biru
                </v-card-text>
              </v-card>
            </v-flex>
            <v-flex d-flex>
              <v-card color="brown" dark tile flat>
                <v-card-text>
                  Coklat >> Coklat >> Coklat >> Coklat
                </v-card-text>
              </v-card>
            </v-flex>
          </v-layout>
        </v-flex>
        
        <v-flex d-flex xs12 sm7>
          <v-layout row wrap>
            <v-flex d-flex>
              <v-card color="indigo lighten-2" dark tile flat>
                <v-card-text>
                    indigo >> indigo >> indigo >> indigo >> indigo
                </v-card-text>
              </v-card>
            </v-flex>
            <v-flex d-flex>
              <v-layout row>
                <v-flex d-flex
                  v-for="n in 2"
                  :key="n"
                >
                  <v-card
                    color="amber lighten-2"
                    tile
                    flat
                  >
                    <v-card-text>amber lighten-2 >> amber lighten-2 >> amber lighten-2 >> amber lighten-2
                    </v-card-text>
                  </v-card>
                </v-flex>
              </v-layout>
            </v-flex>
          </v-layout>
        </v-flex>

        <v-flex d-flex xs12 sm2 child-flex>
          <v-card color="orange lighten-2" tile flat>
            <v-card-text>orange lighten-2 >> orange lighten-2 >> orange lighten-2 >> orange lighten-2
            </v-card-text>
          </v-card>
        </v-flex>
        <v-flex d-flex xs12 sm3>
          <v-card color="red lighten-2" dark tile flat>
            <v-card-text>red lighten-2 >> red lighten-2 >> red lighten-2 >> red lighten-2
            </v-card-text>
          </v-card>
        </v-flex>
      </v-layout>

      <br>
      <template>
        <v-parallax src="https://www.highreshdwallpapers.com/wp-content/uploads/2015/01/Glorious-Autumn-Sunshine-1920x1080.jpg" height="200">
          <v-layout column align-center justify-center>
            <h1 class="white--text">Vuetify.js</h1>
            <h4 class="white--text">Paralax tengah!</h4>
          </v-layout>
        </v-parallax>
      </template>
      <br>

      <v-layout row wrap>
        <v-flex xs10 center>
          <v-card dark color="primary">
            <v-card-text class="px-0">
              The v-container can be used for a center focused page, or given the fluid prop to extend its full width. v-layout is used for separating sections and contains the v-flex. The structure of your layout will be as follows, v-container » v-layout » v-flex. Each part of the grid chain is a flex-box element. The final, v-flex, automatically sets its children to have flex: 1 1 auto.
              <br><br>
              The v-container can be used for a center focused page, or given the fluid prop to extend its full width. v-layout is used for separating sections and contains the v-flex. The structure of your layout will be as follows, v-container » v-layout » v-flex. Each part of the grid chain is a flex-box element. The final, v-flex, automatically sets its children to have flex: 1 1 auto.
              <hr>  
                <v-card-text
                  v-text="lempar">
                </v-card-text>
                <v-card-text
                  v-text="lempar.slice(0, 10)">
                </v-card-text>
            </v-card-text>
          </v-card>
        </v-flex>

        <br>
        <br>
    
        <v-flex xs12 sm12 md12>
          <v-card dark color="primary">
            <v-card-text class="px-0">v-flex xs12</v-card-text>
          </v-card>
        </v-flex>
        <v-flex xs6>
          <v-card dark color="secondary">
            <v-card-text class="px-0">xs6</v-card-text>
          </v-card>
        </v-flex>
        <v-flex xs4>
          <v-card dark color="primary">
            <v-card-text class="px-0">xs4</v-card-text>
          </v-card>
        </v-flex>
        <v-flex xs3>
          <v-card dark color="secondary">
            <v-card-text class="px-0">xs3</v-card-text>
          </v-card>
        </v-flex>
        <v-flex xs2>
          <v-card dark color="primary">
            <v-card-text class="px-0">xs2</v-card-text>
          </v-card>
        </v-flex>
        <v-flex xs1>
          <v-card dark color="secondary">
            <v-card-text class="px-0">xs1</v-card-text>
          </v-card>
        </v-flex>
      </v-layout>

      <br>
      <br>
      <br>
      <br>



      <v-layout row wrap>
        <v-flex xs10 offset-xs1>
          <v-card dark color="purple">
            <v-card-text>xs10 offset-xs1</v-card-text>
          </v-card>
        </v-flex>
        <v-flex xs7 offset-xs0 offset-md2 offset-lg5>
          <v-card dark color="secondary">
            <v-card-text>xs7 offset-xs5</v-card-text>
          </v-card>
        </v-flex>
        <v-flex xs12 sm5 md2>
          <v-card dark color="primary">
            <v-card-text>xs5</v-card-text>
          </v-card>
        </v-flex>
        <v-flex xs12 sm5 md3 offset-xs0 offset-lg2>
          <v-card dark color="green">
            <v-card-text>xs5 offset-xs2</v-card-text>
          </v-card>
        </v-flex>
      </v-layout>

      <template>
        <v-parallax src="https://www.highreshdwallpapers.com/wp-content/uploads/2015/01/Glorious-Autumn-Sunshine-1920x1080.jpg" height="200">
          <v-layout column align-center justify-center>
            <h1 class="white--text">Vuetify.js</h1>
            <h4 class="white--text">Build your application today!</h4>
          </v-layout>
        </v-parallax>
      </template>

      <!-- Grid -->
      <template>
        <v-container grid-list-md text-xs-center>
          <v-layout row wrap>
            <v-flex xs12>
              <v-card dark color="primary">
                <v-card-text class="px-0">12</v-card-text>
              </v-card>
            </v-flex>
            <v-flex xs6 v-for="i in 2" :key="i">
              <v-card dark color="secondary">
                <v-card-text class="px-0">6</v-card-text>
              </v-card>
            </v-flex>
            <v-flex xs4 v-for="i in 3" :key="i">
              <v-card dark color="primary">
                <v-card-text class="px-0">4</v-card-text>
              </v-card>
            </v-flex>
            <v-flex xs3 v-for="i in 4" :key="i">
              <v-card dark color="secondary">
                <v-card-text class="px-0">3</v-card-text>
              </v-card>
            </v-flex>
            <v-flex xs2 v-for="i in 6" :key="i">
              <v-card dark color="primary">
                <v-card-text class="px-0">2</v-card-text>
              </v-card>
            </v-flex>
            <v-flex xs1 v-for="i in 12" :key="i">
              <v-card dark color="secondary">
                <v-card-text class="px-0">1</v-card-text>
              </v-card>
            </v-flex>
          </v-layout>
        </v-container>
      </template>
      <!-- End Grid -->


      <br>
      <br>
      <p>Cookie current = {{ my_cookie }}</p>
      <p>cookie_data_agen = {{ this.$cookies.get("cookie_data_agen") }}</p>
      <p>cookie_data_agen1 = {{ this.$cookies.get("cookie_data_agen1") }}</p>
      <p>cookie_data_agen2 = {{ this.$cookies.get("cookie_data_agen2") }}</p>
      <p>URL = {{ this.$route.path }}</p>
      <br>
    </v-container>
  </v-card>

</template>

<script>
  export default {
    data: () => ({
      lorem: `Lorem ipsum dolor sit amet, mel at clita quando. Te sit oratio vituperatoribus, nam ad ipsum posidonium mediocritatem, explicari dissentiunt cu mea. Repudiare disputationi vim in, mollis iriure nec cu, alienum argumentum ius ad. Pri eu justo aeque torquatos.`,
      lempar: `coba ambil data dari script untuk dipasang dalam postingan.`
    }),
    computed: {
      my_cookie () {
        // check a cookie exist
        if (this.$cookies.isKey("cookie_data_agen2")==false || this.$cookies.get("cookie_data_agen2")=="/grid") {
        //https://www.npmjs.com/package/vue-cookies
        var mypath= this.$route.path
        this.$cookies.set("cookie_data_agen2",mypath, 60 * 60 * 24 * 30) // 1 month after, expire
        return this.$cookies.get("cookie_data_agen2")
        //return this.$cookies.isKey("cookie_data_agen")
        //return this.$route.path
        //return mypath
        } else { return this.$cookies.get("cookie_data_agen2") }
      }
    }
  }
</script>


