/* eslint-disable */
import Vue from 'vue'
import Vuetify from 'vuetify'
import 'vuetify/dist/vuetify.css'
import colors from 'vuetify/es5/util/colors'
import App from './App'
import * as firebase from 'firebase'
import router from './router'
import { store } from './store'
import moment from 'moment'
import DateFilter from './filters/date'
import AlertCmp from './components/Shared/Alert.vue'
import OmengCSS from './components/Shared/Omeng.css'
import EditMeetupDetailsDialog from './components/Meetup/Edit/EditMeetupDetails.vue'
import EditMeetupDateDialog from './components/Meetup/Edit/EditMeetupDate.vue'
import EditMeetupTimeDialog from './components/Meetup/Edit/EditMeetupTime.vue'

import EditAgenDetailsDialog from './components/Agen/Edit/EditAgenDetails.vue'
import EditCustomerDetailsDialog from './components/Customer/Edit/EditCustomerDetails.vue'
import AddCustomerDialog from './components/Customer/AddCustomerDialog.vue'

import RegisterDialog from './components/Meetup/Registration/RegisterDialog.vue'
import VueMce from 'vue-mce'

import VueYoutube from 'vue-youtube'
import VAnimateCss from 'v-animate-css'

import SocialSharing from 'vue-social-sharing'


//REGISTER
Vue.use(Vuetify, { // harus diatas posisinya
  theme: {
    primary: '#8bc34a', //hijau kurma
    secondary: '#81C784', //hijau muda //'#b0bec5',
    accent: '#8c9eff',
    error: '#b71c1c',

    info: '#2196F3',
    success: '#4CAF50',
    warning: '#FFC107',

    ungu: '#9C27B0',
    biru_tua: '#3F51B5',
    biru_muda: '#64B5F6',
    hijau_muda: '#81C784',
    merah_muda: '#E57373',

    putih: '#FAFAFA',
    hitam: '#81C784',
    merah: '#81C784',
    biru: '#81C784',
    kuning: '#81C784',
    hijau: '#81C784',
    hijau_muda: '#81C784'
  }
})

Vue.config.productionTip = false

Vue.filter('date', DateFilter)
Vue.component('app-alert', AlertCmp)
Vue.component('app-edit-meetup-details-dialog', EditMeetupDetailsDialog)
Vue.component('app-edit-meetup-date-dialog', EditMeetupDateDialog)
Vue.component('app-edit-meetup-time-dialog', EditMeetupTimeDialog)

Vue.component('app-edit-agen-details-dialog', EditAgenDetailsDialog)

Vue.component('app-edit-customer-details-dialog', EditCustomerDetailsDialog)
Vue.component('app-add-customer-dialog', AddCustomerDialog)

Vue.component('app-meetup-register-dialog', RegisterDialog)
Vue.use(VueMce)
Vue.use(VueYoutube)
Vue.use(VAnimateCss)
Vue.use(SocialSharing)

import * as VueGoogleMaps from 'vue2-google-maps'
Vue.use(VueGoogleMaps, {
  load: {
    key: 'AIzaSyAkjrcigUpkOgd5rF4fX-gUXXJAGCf28L0',
    //libraries: 'places,drawing,visualization', // This is required if you use the Autocomplete plugin
    // OR: libraries: 'places,drawing'
    // OR: libraries: 'places,drawing,visualization'
    // (as you require)
  }
})


new Vue({
  el: '#app',
  router,
  store,
  render: h => h(App),
  created () {
    firebase.initializeApp({
      apiKey: "AIzaSyCLSbqho7tei6ncmZT8_OE0Kn-xcxch5iY",
      authDomain: "academind-meetup.firebaseapp.com",
      databaseURL: "https://academind-meetup.firebaseio.com",
      projectId: "academind-meetup",
      storageBucket: "gs://academind-meetup.appspot.com",
    })
    firebase.auth().onAuthStateChanged((user) => {
      if (user) {
        this.$store.dispatch('autoSignIn',user)
        this.$store.dispatch('fetchUserData')
      }
    })
    this.$store.dispatch('loadMeetups')
    this.$store.dispatch('loadAgen_db')
    this.$store.dispatch('loadCustomer_db')
  }
})




