<template>
  <v-dialog max-width="500px" persistent v-model="editDialog">
    <v-btn fab dark small color="red" accent slot="activator">
      <v-icon>edit</v-icon>
    </v-btn>
    <v-card dark>
      <v-container>
        <v-layout row wrap>
          <v-flex xs12>
            <h3 class="primary--text">Edit Customer Details</h3>
          </v-flex>
        </v-layout>
        <v-divider></v-divider>
        <v-layout row wrap>
          <v-flex xs12>
            <v-card-text>
              <v-text-field
              box dark
              label="Full Name"
              v-model="editednama_customer"
              required
              id="nama_customer"
            ></v-text-field>
            <v-text-field
              box dark
              label="Email Address"
              placeholder="customer@alifagency.com"
              v-model="editedemail_customer"
              required
              id="email_customer"
            ></v-text-field>
            <v-text-field
              box dark
              label="Nomor Telepon"
              placeholder="628**********"
              v-model="editedtelepon_customer"
              required
              id="telepon_customer"
            ></v-text-field>
            <v-text-field
              box dark
              label="Nomor Whatsapp"
              placeholder="628**********"
              v-model="editedwhatsapp_customer"
              required
              id="whatsapp_customer"
            ></v-text-field>
  
            <v-text-field
              box dark
              label="Alamat Rumah"
              placeholder="Jl. Langgar 80 B, Kel. Kemiri Muka,Kec.Beji, Kota Depok, Prov. Jawa Barat"
              multi-line
              v-model="editedalamat_customer"
              id="alamat_customer"
              counter="219"
              required
            ></v-text-field>
            
            </v-card-text>
          </v-flex>
        </v-layout>
        <v-divider></v-divider>
        <v-layout row wrap>
          <v-flex xs12>
            <v-card-actions>
              <v-spacer></v-spacer>
              <router-view :key="$route.path"></router-view>
              <v-btn 
                class="red--text darken-1" 
                @click="onDeleteCustomer"
              >Delete</v-btn>
              <router-view :key="$route.path"></router-view>
              <v-btn 
                class="blue--text darken-1" 
                @click="editDialog = false"
              >Cancel</v-btn>
              <router-view :key="$route.path"></router-view>
              <v-btn 
                class="green--text darken-1" 
                @click="onSaveChanges"
              >Save</v-btn>
              
            </v-card-actions>
          </v-flex>
        </v-layout>
      </v-container>
    </v-card>
  </v-dialog>
</template>

<script>
  export default {
    props:['customer'],
    data () {
      return {
        max25chars: (v) => v.length <= 25 || 'Input too long!',
        dialog_add: false,
        editDialog: false,
        
        editednama_customer: this.customer.nama_customer,
        editedemail_customer: this.customer.email_customer,
        editedtelepon_customer: this.customer.telepon_customer,
        editedwhatsapp_customer: this.customer.whatsapp_customer,
        editedalamat_customer: this.customer.alamat_customer
      }

    },
    methods:{
      onSaveChanges () {
        if (this.editednama_customer.trim() === '' || 
            this.editedemail_customer.trim() === '' || 
            this.editedtelepon_customer.trim() === '' ||
            this.editedwhatsapp_customer.trim() === '' || 
            this.editedalamat_customer.trim() === ''
          ) {
          return
        }
        this.editDialog = false
        this.$store.dispatch('updateCustomerData', {
          id: this.customer.id,
          nama_customer: this.editededitednama_customer,
          email_customer: this.editedemail_customer,
          telepon_customer: this.editedtelepon_customer,
          whatsapp_customer: this.editedwhatsapp_customer,
          alamat_customer: this.editedalamat_customer
        })
      },
      onDeleteCustomer () {
        this.editDialog = false
        this.$store.dispatch('deleteCustomerData', {
          id: this.customer.id,
          nama_customer: null,
          email_customer: null,
          telepon_customer: null,
          whatsapp_customer: null,
          alamat_customer: null
        })
        this.$router.push('/customer')
      }
    }
  }
</script>


