<template>
  <v-card>

      <template>
        <v-parallax :src="require('@/components/img/kurma_piring.jpg')" height="300">
          <v-layout column align-center justify-center>
            <h2 class="white--text">DAFTAR AGEN</h2>
            <h1 class="white--text">ALIF AGENCY</h1>
            <h3 class="white--text">Berbagi tanpa batas</h3>
            <h4 class="white--text">Tapi jangan sampai habis semua</h4>
          </v-layout>
        </v-parallax>
      </template>
    
    <v-card-title>
      <v-chip
        color="primary"
        text-color="white"
        @click.native.stop="dialog_add = !dialog_add"
      >
        <v-avatar>
          <v-icon>account_circle</v-icon>
        </v-avatar>
        Add
      </v-chip>

    
      <v-spacer></v-spacer>
      <v-text-field
        append-icon="search"
        label="Search"
        single-line
        hide-details
        top
        v-model="search"
      ></v-text-field>
    </v-card-title>

    <hr>

    <v-data-table
        v-bind:headers="headers"
        v-bind:items="agen"
        v-bind:search="search"
      >

      <template slot="items" slot-scope="props">
        <td>
          <v-edit-dialog lazy> {{ props.item.nama_agen }}
            <v-text-field
              slot="input"
              label="Edit"
              v-model="props.item.nama_agen"
              single-line
              counter
              :rules="[max25chars]"
            ></v-text-field>
          </v-edit-dialog>
        </td>
        <td class="text-xs-right">{{ props.item.email_agen }}</td>
        <td class="text-xs-right">{{ props.item.telepon_agen }}</td>
        <td class="text-xs-right">{{ props.item.whatsapp_agen }}</td>
        <td class="text-xs-right">{{ props.item.alamat_agen }}</td>
        <td class="text-xs-right">{{ props.item.ktp_agen }}</td>
        <td class="text-xs-right">{{ props.item.kode_agen }}</td>
        <td class="text-xs-right">
          <v-edit-dialog
            @open="tmp = props.item.bos_agen"
            @save="props.item.bos_agen = tmp || props.item.bos_agen"
            large
            lazy
          >
            <div>{{ props.item.bos_agen }}</div>
            <div slot="input" class="mt-3 title">Update bos_agen</div>
            <v-text-field
              slot="input"
              label="Edit"
              v-model="tmp"
              single-line
              counter
              autofocus
              :rules="[max25chars]"
            ></v-text-field>
          </v-edit-dialog>
        </td>


        <td>
          <!--
          <v-spacer></v-spacer>
          <v-btn fab dark small color="red"><v-icon>delete</v-icon></v-btn>
          <v-btn fab dark small color="green"><v-icon>edit</v-icon></v-btn>
          -->
          <v-spacer></v-spacer>
          <app-edit-agen-details-dialog :agen="props.item"></app-edit-agen-details-dialog>
          <router-view :key="$route.path"></router-view>
        </td>


      </template >

        <template slot="pageText" slot-scope="{ pageStart, pageStop }">
          From {{ pageStart }} to {{ pageStop }}
        </template>
    </v-data-table>

    <v-dialog v-model="dialog_add" max-width="500px">
      <v-card dark>
        <v-card-title class="headline">
          Penambahan Agen
        </v-card-title>
        <v-card-text>
          <v-text-field
            box dark
            label="Full Name"
            placeholder="Muhammad Yahya"
            v-model="nama_agen"
            required
            id="nama_agen"
            :rules="[() => !!nama_agen || 'This field is required']"
            :error-messages="errorMessages"
          ></v-text-field>
          <v-text-field
            box dark
            label="Email Address"
            placeholder="agen@alifagency.com"
            v-model="email_agen"
            required
            id="email_agen"
            :rules="[() => !!email_agen || 'This field is required']"
            :error-messages="errorMessages"
          ></v-text-field>
          <v-text-field
            box dark
            label="Nomor Telepon"
            placeholder="628**********"
            v-model="telepon_agen"
            required
            id="telepon_agen"
            :rules="[() => !!telepon_agen || 'This field is required']"
            :error-messages="errorMessages"
          ></v-text-field>
          <v-text-field
            box dark
            label="Nomor Whatsapp"
            placeholder="628**********"
            v-model="whatsapp_agen"
            required
            id="whatsapp_agen"
            :rules="[() => !!whatsapp_agen || 'This field is required']"
            :error-messages="errorMessages"
          ></v-text-field>

          <v-text-field
            box dark
            label="Alamat Rumah"
            placeholder="Jl. Langgar 80 B, Kel. Kemiri Muka,Kec.Beji, Kota Depok, Prov. Jawa Barat"
            :rules="[
              () => !!alamat_agen || 'This field is required',
              () => !!alamat_agen && alamat_agen.length <= 219 || 'Address must be less than 219 characters',
              this.addressCheck
            ]"
            multi-line
            v-model="alamat_agen"
            id="alamat_agen"
            counter="219"
            required
          ></v-text-field>

          <v-text-field
            box dark
            label="Nomor KTP"
            placeholder="789456132456"
            v-model="ktp_agen"
            required
            id="ktp_agen"
            :rules="[() => !!ktp_agen || 'This field is required']"
            :error-messages="errorMessages"
          ></v-text-field>
          <v-text-field
            box dark
            label="Kode Agen"
            placeholder="00*"
            v-model="kode_agen"
            required
            id="kode_agen"
            :rules="[() => !!kode_agen || 'This field is required']"
            :error-messages="errorMessages"
          ></v-text-field>
          <v-text-field
            box dark
            label="Bos Agen"
            placeholder="Yahya"
            v-model="bos_agen"
            required
            id="bos_agen"
            :rules="[() => !!bos_agen || 'This field is required']"
            :error-messages="errorMessages"
          ></v-text-field>

        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>
          
          <v-btn small color="primary" flat @click.stop="dialog_add=false">Close</v-btn>
          <v-btn small color="primary" flat @click="onCreateAgen">Submit</v-btn>
          <router-view :key="$route.path"></router-view>
        </v-card-actions>
      </v-card>

  </v-dialog>



  </v-card>
</template>

<script>
  export default {
    data () {
      return {
        dialog_add: false,
        max25chars: (v) => v.length <= 25 || 'Input too long!',
        tmp: '',
        search: '',
        pagination: {},
        headers: [
          {
            text: 'Nama',
            align: 'left',
            sortable: true,
            value: 'nama_agen'
          },
          { text: 'Email', value: 'email_agen' },
          { text: 'Telepon', value: 'telepon_agen' },
          { text: 'Whatsapp', value: 'whatsapp_agen' },
          { text: 'Alamat', value: 'alamat_agen' },
          { text: 'No.KTP', value: 'ktp_agen' },
          { text: 'Kode', value: 'kode_agen' },
          { text: 'Agency', value: 'bos_agen' }
        ],
        errorMessages: [],

        nama_agen: null,
        email_agen: null,
        telepon_agen: null,
        whatsapp_agen: null,
        alamat_agen: null,
        ktp_agen: null,
        kode_agen: null,
        bos_agen: null,
        formHasErrors: false
          
      }
    },
    created () {
      this.fetchData()
    },
    computed: {
      agen () {
        return this.$store.getters.loadedAgen_db
      },
      form () {
        return {
          nama_agen: this.nama_agen,
          email_agen: this.email_agen,
          telepon_agen: this.telepon_agen,
          whatsapp_agen: this.whatsapp_agen,
          alamat_agen: this.alamat_agen,
          ktp_agen: this.ktp_agen,
          kode_agen: this.kode_agen,
          bos_agen: this.bos_agen,
          country: this.country
        }
      },
      userIsAuthenticated () {
        return this.$store.getters.user !== null && this.$store.getters.user !== undefined
      },
    },

    watch: {
      name () {
        this.errorMessages = []
      }
    },

    methods: {
      fetchData () {
       // fetch data
       
      },
      addressCheck () {
        this.errorMessages = this.alamat_agen && !this.nama_agen
          ? ['Hey! I\'m required']
          : []

        return true
      },
      resetForm () {
        this.errorMessages = []
        this.formHasErrors = false

        Object.keys(this.form).forEach(f => {
          this.$refs[f].reset()
        })
      },
      onCreateAgen () {
        this.dialog_add = false
        this.formHasErrors = false

        Object.keys(this.form).forEach(f => {
          if (!this.form[f]) this.formHasErrors = true

          //this.$refs[f].validate(true)
        })
        if (this.nama_agen.trim() === '' || 
            this.email_agen.trim() === '' || 
            this.telepon_agen.trim() === '' ||
            this.whatsapp_agen.trim() === '' || 
            this.alamat_agen.trim() === '' || 
            this.ktp_agen.trim() === '' || 
            this.kode_agen.trim() === '' 
          ) {
          return
        }
        const agenData = {
          nama_agen: this.nama_agen,
          email_agen: this.email_agen,
          telepon_agen: this.telepon_agen,
          whatsapp_agen: this.whatsapp_agen,
          alamat_agen: this.alamat_agen,
          ktp_agen: this.ktp_agen,
          kode_agen: this.kode_agen,
          bos_agen: this.bos_agen
        }
        this.$store.dispatch('createAgen', agenData)
        this.$router.push('/agen')

      }

    }
  }
</script>



