<template>
    <v-card>
      <v-card-text>
        <v-container grid-list-md>
          <v-layout row wrap>
            <!-- <v-flex xs12 sm6 md4> -->

                <!-- https://github.com/nicolasbeauvais/vue-social-sharing -->
                <social-sharing url="https://vuejs.org/"
                title="The Progressive JavaScript Framework"
                description="Intuitive, Fast and Composable MVVM for building interactive interfaces."
                quote="Vue is a progressive framework for building user interfaces."
                hashtags="vuejs,javascript,framework"
                twitter-user="vuejs"
                :networks="overriddenNetworks"
                v-cloak inline-template>
                <div>
                  <ul class="headline">
                    <li>
                      <network network="email" id="email">
                        <i class="fa fa-fw fa-envelope"></i> Email
                      </network>
                    </li>
                    <li>
                      <network network="facebook" id="facebook">
                        <i class="fa fa-fw fa-facebook"></i> Facebook
                      </network>
                    </li>
                    <li>
                      <network network="googleplus" id="googleplus">
                        <i class="fa fa-fw fa-google-plus"></i> Google +
                      </network>
                    </li>
                    <li>
                      <network network="linkedin" id="linkedin">
                        <i class="fa fa-fw fa-linkedin"></i> LinkedIn
                      </network>
                    </li>
                    <li>
                      <network network="pinterest" id="pinterest">
                        <i class="fa fa-fw fa-pinterest"></i> Pinterest
                      </network>
                    </li>
                    <li>
                      <network network="reddit" id="reddit">
                        <i class="fa fa-fw fa-reddit"></i> Reddit
                      </network>
                    </li>
                    <li>
                      <network network="twitter" id="twitter">
                        <i class="fa fa-fw fa-twitter"></i> Twitter
                      </network>
                    </li>
                    <li>
                      <network network="viber" id="viber">
                        <i class="fa fa-fw fa-phone-square"></i> Viber
                      </network>
                    </li>
                    <li>
                      <network network="vk" id="vk">
                        <i class="fa fa-fw fa-vk"></i> VKontakte
                      </network>
                    </li>
                    <li>
                      <network network="weibo" id="weibo">
                        <i class="fa fa-fw fa-weibo"></i> Weibo
                      </network>
                    </li>
                    <li>
                      <network network="whatsapp" id="whatsapp">
                        <i class="fa fa-fw fa-whatsapp"></i> Whatsapp
                      </network>
                    </li>
                    <li>
                      <network network="telegram" id="telegram">
                        <i class="fa fa-fw fa-telegram"></i> Telegram
                      </network>
                    </li>
                    <li>
                      <network network="line" id="line">
                          <i class="fa fa-fw fa-line"></i>
                          Line
                      </network>
                    </li>
                    <li>
                      <network network="skype" id="skype">
                        <i class="fa fa-fw fa-skype"></i> Skype
                      </network>
                    </li>
                    <li>
                      <network network="odnoklassniki" id="odnoklassniki">
                        <i class="fa fa-fw fa-odnoklassniki"></i> Ok
                      </network>
                    </li>
                    <li>
                      <network network="test" id="test">
                        <i class="fa fa-fw fa-warning"></i> Test custom network
                      </network>
                    </li>
                  </ul>
                </div>
              </social-sharing>


            <!-- </v-flex> -->
          </v-layout>
        </v-container>
      </v-card-text>
    </v-card>
  </template>

  <script>
    
  export default {
    data: () => ({
      slider: 56,
      tile: false,
      overriddenNetworks: {
        "test": {
          "sharer": "https://google.com",
          "type": "popup"
        },
      }
    }),

    

    
  }
</script>

<style>
  ul {
    padding: 0;
    list-style: none;
  }
  ul i.fa {
    margin-right: 5px;
  }
</style>


