<template>
  <v-dialog max-width="500px" persistent v-model="editDialog">
    <v-btn fab dark small color="red" accent slot="activator">
      <v-icon>edit</v-icon>
    </v-btn>
    <v-card dark>
      <v-container>
        <v-layout row wrap>
          <v-flex xs12>
            <h3 class="primary--text">Edit Agen Details</h3>
          </v-flex>
        </v-layout>
        <v-divider></v-divider>
        <v-layout row wrap>
          <v-flex xs12>
            <v-card-text>
              <v-text-field
              box dark
              label="Full Name"
              v-model="editednama_agen"
              required
              id="nama_agen"
            ></v-text-field>
            <v-text-field
              box dark
              label="Email Address"
              placeholder="agen@alifagency.com"
              v-model="editedemail_agen"
              required
              id="email_agen"
            ></v-text-field>
            <v-text-field
              box dark
              label="Nomor Telepon"
              placeholder="628**********"
              v-model="editedtelepon_agen"
              required
              id="telepon_agen"
            ></v-text-field>
            <v-text-field
              box dark
              label="Nomor Whatsapp"
              placeholder="628**********"
              v-model="editedwhatsapp_agen"
              required
              id="whatsapp_agen"
            ></v-text-field>
  
            <v-text-field
              box dark
              label="Alamat Rumah"
              placeholder="Jl. Langgar 80 B, Kel. Kemiri Muka,Kec.Beji, Kota Depok, Prov. Jawa Barat"
              multi-line
              v-model="editedalamat_agen"
              id="alamat_agen"
              counter="219"
              required
            ></v-text-field>
  
            <v-text-field
              box dark
              label="Nomor KTP"
              placeholder="789456132456"
              v-model="editedktp_agen"
              required
              id="ktp_agen"
            ></v-text-field>
            <v-text-field
              box dark
              label="Kode Agen"
              placeholder="00*"
              v-model="editedkode_agen"
              required
              id="kode_agen"
            ></v-text-field>
            <v-text-field
              box dark
              label="Bos Agen"
              placeholder="Yahya"
              v-model="editedbos_agen"
              required
              id="bos_agen"
            ></v-text-field>
            
            </v-card-text>
          </v-flex>
        </v-layout>
        <v-divider></v-divider>
        <v-layout row wrap>
          <v-flex xs12>
            <v-card-actions>
              <v-spacer></v-spacer>
              <router-view :key="$route.path"></router-view>
              <v-btn 
                class="red--text darken-1" 
                @click="onDeleteAgen"
              >Delete</v-btn>
              <router-view :key="$route.path"></router-view>
              <v-btn 
                class="blue--text darken-1" 
                @click="editDialog = false"
              >Cancel</v-btn>
              <router-view :key="$route.path"></router-view>
              <v-btn 
                class="green--text darken-1" 
                @click="onSaveChanges"
              >Save</v-btn>
              
              
            </v-card-actions>
          </v-flex>
        </v-layout>
      </v-container>
    </v-card>
  </v-dialog>
</template>

<script>
  export default {
    props:['agen'],
    data () {
      return {
        max25chars: (v) => v.length <= 25 || 'Input too long!',
        dialog_add: false,
        editDialog: false,
        
        editednama_agen: this.agen.nama_agen,
        editedemail_agen: this.agen.email_agen,
        editedtelepon_agen: this.agen.telepon_agen,
        editedwhatsapp_agen: this.agen.whatsapp_agen,
        editedalamat_agen: this.agen.alamat_agen,
        editedktp_agen: this.agen.ktp_agen,
        editedkode_agen: this.agen.kode_agen,
        editedbos_agen: this.agen.bos_agen,
        editedcountry: this.agen.country
      }

    },
    methods:{
      onSaveChanges () {

        if (this.editednama_agen.trim() === '' || 
            this.editedemail_agen.trim() === '' || 
            this.editedtelepon_agen.trim() === '' ||
            this.editedwhatsapp_agen.trim() === '' || 
            this.editedalamat_agen.trim() === '' || 
            this.editedktp_agen.trim() === '' || 
            this.editedkode_agen.trim() === '' 
          ) {
          return
        }
        this.editDialog = false
        this.$store.dispatch('updateAgenData', {
          id: this.agen.id,
          nama_agen: this.editededitednama_agen,
          email_agen: this.editedemail_agen,
          telepon_agen: this.editedtelepon_agen,
          whatsapp_agen: this.editedwhatsapp_agen,
          alamat_agen: this.editedalamat_agen,
          ktp_agen: this.editedktp_agen,
          kode_agen: this.editedkode_agen,
          bos_agen: this.editedbos_agen
        })

      },
      onDeleteAgen () {
        this.editDialog = false
        this.$store.dispatch('deleteAgenData', {
          id: this.agen.id,
          nama_agen: null,
          email_agen: null,
          telepon_agen: null,
          whatsapp_agen: null,
          alamat_agen: null,
          ktp_agen: null,
          kode_agen: null,
          bos_agen: null
        })
        this.$router.push('/agen')

      }

    }
  }
</script>


