<template>
  <v-container>
    <v-layout row>
      <v-flex xs12 sm6 offset-sm3>
        <h3 class="secondary--text">Create a new Meetup</h3>
      </v-flex>
    </v-layout>
    <v-layout row>
      <v-flex xs12>
        <form @submit.prevent="onCreateMeetup">
          <v-layout row>
            <v-flex xs12 sm6 offset-sm3>
              <v-text-field
                name="title"
                label="Title"
                id="title"
                v-model="title"
                required></v-text-field>
            </v-flex>
          </v-layout>
          <v-layout row>
            <v-flex xs12 sm6 offset-sm3>
              <v-text-field
                name="location"
                label="Location"
                id="location"
                v-model="location"
                required></v-text-field>
            </v-flex>
          </v-layout>
          <v-layout row>
            <v-flex xs12 sm6 offset-sm3>
              <v-text-field
                name="description"
                label="Description"
                id="description"
                multi-line
                v-model="description"
                required></v-text-field>
            </v-flex>
          </v-layout>

          <v-layout row>
            <v-flex xs12 sm6 offset-sm3>
              <v-btn round class="primary" @click="onPickFile">Upload Image</v-btn>
              <input 
              type="file" 
              style="display: none" 
              ref="fileInput" 
              accept="image/*"
              @change="onFilePicked">
            </v-flex>
          </v-layout>
          <v-layout row>
            <v-flex xs12 sm6 offset-sm3 center>
                <img :src="imageUrl" height="190px">
            </v-flex>
          </v-layout>

          <p></p>
          <p></p>

          <v-layout row>
            <v-flex xs12 sm6 offset-sm3>
              <h3>Choose a Date & Time</h3>
            </v-flex>
          </v-layout>
          <v-layout row class="mb-2">
            <v-flex xs12 sm6 offset-sm3>
              <v-date-picker v-model="date"></v-date-picker>
              <p>{{ date }}</p>
            </v-flex>
          </v-layout>
          <v-layout row class="mb-2">
              <v-flex xs12 sm6 offset-sm3>
                <v-time-picker v-model="time" format="24hr"></v-time-picker>
                <p>{{ time }}</p>
              </v-flex>
            </v-layout>
  
          <v-layout row>
            <v-flex xs12 sm6 offset-sm3>
              <v-btn 
              class="primary" 
              :disabled="!formIsValid"
              type="submit">Create Meetup</v-btn>
              {{ date }}, {{ time }}
            </v-flex>
          </v-layout>
          </form>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
  export default {
    data () {
      return {
        title: '',
        location: '',
        imageUrl: '',
        description: '',
        date: '',
        time: new Date(),
        image: null
      }
    },
    computed:{
      formIsValid () {
        return this.title !== '' && 
        this.location !== '' && 
        this.imageUrl !== '' && 
        this.description !== ''
      }
    },
    methods: {
      onCreateMeetup () {
        if (!this.formIsValid) {
          return 
        }
        if (!this.image) {
          return
        }
        const meetupData = {
          title: this.title,
          location: this.location,
          image: this.image,
          description: this.description,
          date: this.date, //submittableDateTime
          time: this.time //submittableDateTime
        }
        this.$store.dispatch('createMeetup', meetupData)
        this.$router.push('/meetups')
      },
      onPickFile () {
        this.$refs.fileInput.click()
      },
      onFilePicked (event) {
        const files = event.target.files
        let filename = files[0].name
        if (filename.lastIndexOf('.') <= 0) {
          return alert('Please add a valid file!')
        }
        const fileReader = new FileReader()
        fileReader.addEventListener('load', () => {
          this.imageUrl = fileReader.result
        })
        fileReader.readAsDataURL(files[0])
        this.image = files[0]
      }
    }
  }
</script>


