import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Meetups from '@/components/Meetup/Meetups'
import CreateMeetup from '@/components/Meetup/CreateMeetup'

import Profile from '@/components/User/Profile'
import CreateProfile from '@/components/User/CreateProfile'
import Signin from '@/components/User/Signin'
//import Signup from '@/components/User/Signup'

import Meetup from '@/components/Meetup/Meetup'

import Banten from '@/components/Lokasi/Banten'
import Cipanas from '@/components/Lokasi/Cipanas'
import Cirebon from '@/components/Lokasi/Cirebon'
import Jasinga from '@/components/Lokasi/Jasinga'
import Jonggol from '@/components/Lokasi/Jonggol'
import Tanjungsari from '@/components/Lokasi/Tanjungsari'


import Grid from '@/components/TestTest/Grid'
import DataTable from '@/components/TestTest/DataTable'
import Link1 from '@/components/TestTest/Link1'
import Link2 from '@/components/TestTest/Link2'
import Link3 from '@/components/TestTest/Link3'
import Link4 from '@/components/TestTest/Link4'
import Link5 from '@/components/TestTest/Link5'

import DaftarAgen from '@/components/Agen/DaftarAgen'
//import TambahAgen from '@/components/Agen/TambahAgen'
import DaftarCustomer from '@/components/Customer/DaftarCustomer'

import AuthGuard from './auth-guard'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/', 
                //redirect: '/agen',
      name: 'Home',
      component:Home
    },
    {
      path: '/meetups',
      name: 'Meetups',
      component: Meetups
    },
    {
      path: '/meetups/:id',
      name: 'Meetup',
      props: true,
      component: Meetup
    },
    

    //halaman Agen sementara dibuat
    {
      path: '/agen',
      name: 'DaftarAgen',
      component: DaftarAgen
      //beforeEnter: AuthGuard
    },
    {
      path: '/customer',
      name: 'DaftarCustomer',
      component: DaftarCustomer
      //beforeEnter: AuthGuard
    },
    /*{
      path: '/agen/tambah',
      name: 'TambahAgen',
      component: TambahAgen,
      beforeEnter: AuthGuard
    },*/

    //halaman LOKASI
    {
      path: '/banten',
      name: 'Banten',
      component: Banten
    },
    {
      path: '/cipanas',
      name: 'Cipanas',
      component: Cipanas
    },
    {
      path: '/cirebon',
      name: 'Cirebon',
      component: Cirebon
    },
    {
      path: '/jasinga',
      name: 'Jasinga',
      component: Jasinga
    },    {
      path: '/jonggol',
      name: 'Jonggol',
      component: Jonggol
    },    {
      path: '/tanjungsari',
      name: 'Tanjungsari',
      component: Tanjungsari
    },

    //halaman test-test
    {
      path: '/grid',
      name: 'Grid',
      component: Grid
    },
    {
      path: '/datatable',
      name: 'DataTable',
      component: DataTable
    },
    {
      path: '/link1',
      name: 'Link1',
      component: Link1
    },
    {
      path: '/link2',
      name: 'Link2',
      component: Link2
    },    {
      path: '/link3',
      name: 'Link3',
      component: Link3
    },    {
      path: '/link4',
      name: 'Link4',
      component: Link4
    },    {
      path: '/link5',
      name: 'Link5',
      component: Link5
    },

    //halaman Profil belum selesai
    {
      path: '/profile',
      name: 'Profile',
      component: Profile,
      beforeEnter: AuthGuard
    },
    {
      path: '/profile/new',
      name: 'CreateProfile',
      component: CreateProfile,
      beforeEnter: AuthGuard
    },

    //halaman Authurization
    {
      path: '/meetup/new',
      name: 'CreateMeetup',
      component: CreateMeetup,
      beforeEnter: AuthGuard
    },
    // {
    //   path: '/signup',
    //   name: 'Signup',
    //   component: Signup
    // },
    {
      path: '/signin',
      name: 'Signin',
      component: Signin
    }
  ],
  mode: 'history' //menghapus tanda # pada url
})



