import * as firebase from 'firebase'

export default {
  state: {
    loadedAgen_db: [
      /*,
      { 
        id: '', 
        nama_agen: '',
        email_agen: '',
        telepon_agen: '',
        whatsapp_agen: '',
        alamat_agen: '',
        ktp_agen: '',
        kode_agen: '',
        bos_agen: ''  
      }
      {
        id: '111', 
        nama_agen: 'Andi Chairil Furqan',
        email_agen: 'chairil@alifagency.com',
        telepon_agen: '628915656565',
        whatsapp_agen: '628915656565',
        alamat_agen: 'Depok',
        ktp_agen: '75489123654879',
        kode_agen: '1',
        bos_agen: '1'
      },
      {
        id: '222', 
        nama_agen: 'Muhammad Yahya',
        email_agen: 'yahya@alifagency.com',
        telepon_agen: '628915656567',
        whatsapp_agen: '628915656567',
        alamat_agen: 'Langgar',
        ktp_agen: '75489123654875',
        kode_agen: '2',
        bos_agen: '2'
      },
      {
        id: '333', 
        nama_agen: 'Abdul Rahman',
        email_agen: 'omeng@alifagency.com',
        telepon_agen: '628915656568',
        whatsapp_agen: '628915656568',
        alamat_agen: 'Bogor',
        ktp_agen: '75489123654876',
        kode_agen: '3',
        bos_agen: '3'
      }
      */

    ]
  },
  mutations: {
    setLoadedAgen_db (state, payload) {
      state.loadedAgen_db = payload
    },
    createAgen (state, payload) {
      state.loadedAgen_db.push(payload)
    },
    updateAgen (state, payload) {
      const agen = state.loadedAgen_db.find(agen => {
        return agen.id === payload.id
      })
      if (payload.nama_agen) { agen.nama_agen = payload.nama_agen }
      if (payload.email_agen) { agen.email_agen = payload.email_agen }
      if (payload.telepon_agen) { agen.telepon_agen = payload.telepon_agen }
      if (payload.whatsapp_agen) { agen.whatsapp_agen = payload.whatsapp_agen }
      if (payload.alamat_agen) { agen.alamat_agen = payload.alamat_agen }
      if (payload.ktp_agen) { agen.ktp_agen = payload.ktp_agen }
      if (payload.kode_agen) { agen.kode_agen = payload.kode_agen }
      if (payload.bos_agen) { agen.bos_agen = payload.bos_agen }
    },
    deleteAgen (state, payload) {
      const agen = state.loadedAgen_db.find(agen => {
        return agen.id === payload.id
      })
      if (payload.nama_agen) { agen.nama_agen = null }
      if (payload.email_agen) { agen.email_agen = null }
      if (payload.telepon_agen) { agen.telepon_agen = null }
      if (payload.whatsapp_agen) { agen.whatsapp_agen = null }
      if (payload.alamat_agen) { agen.alamat_agen = null }
      if (payload.ktp_agen) { agen.ktp_agen = null }
      if (payload.kode_agen) { agen.kode_agen = null }
      if (payload.bos_agen) { agen.bos_agen = null }
      if (payload.creatorId) { agen.creatorId = null }
    }
  },
  actions: {
    loadAgen_db ({commit}) {
      commit('setLoading', true)
      firebase.database().ref('agen_db').once('value')
      .then((data) => {
        const agen_db = []
        const obj = data.val()
        for (let key in obj) {
          agen_db.push({
            id: key,
            nama_agen: obj[key].nama_agen,
            email_agen: obj[key].email_agen,
            telepon_agen: obj[key].telepon_agen,
            whatsapp_agen: obj[key].whatsapp_agen,
            alamat_agen: obj[key].alamat_agen,
            ktp_agen: obj[key].ktp_agen,
            kode_agen: obj[key].kode_agen,
            bos_agen: obj[key].bos_agen,
            creatorId: obj[key].creatorId
          })
        }
        //console.log(data)
        commit('setLoadedAgen_db', agen_db)
        commit('setLoading', false)
      })
      .catch(
        (error) => {
          console.log(error)
          commit('setLoading', false)
        }
      )
    },
    createAgen ({commit, getters}, payload) {
      const agen = {
        nama_agen: payload.nama_agen,
        email_agen: payload.email_agen,
        telepon_agen: payload.telepon_agen,
        whatsapp_agen: payload.whatsapp_agen,
        alamat_agen: payload.alamat_agen,
        ktp_agen: payload.ktp_agen,
        kode_agen: payload.kode_agen,
        bos_agen: payload.bos_agen,
        creatorId: getters.user.id
      }
      let imageUrl
      let key
      firebase.database().ref('agen_db').push(agen)
      .then((data) => {
        key = data.key
        return key
      })
      .then(key => {
        const filename = payload.image.name
        const ext = filename.slice(filename.lastIndexOf('.'))
        return firebase.storage().ref('agen_db/' + key + '.' + ext).put(payload.image)
      })
      .then(fileData => {
        imageUrl = fileData.metadata.downloadURLs[0]
        return firebase.database().ref('agen_db').child(key).update({imageUrl: imageUrl})
      })
      .then(() => {
        commit('createAgen', {
          ...agen,
          imageUrl: imageUrl,
          id: key
        })
      })
      .catch((error) => {
        console.log(error)
      })
      // Reach out to firebase and store it
    },
    updateAgenData ({commit}, payload) {
      commit('setLoading', true)
      const updateObj = {}
      if (payload.nama_agen) { updateObj.nama_agen = payload.nama_agen }
      if (payload.email_agen) { updateObj.email_agen = payload.email_agen }
      if (payload.telepon_agen) { updateObj.telepon_agen = payload.telepon_agen }
      if (payload.whatsapp_agen) { updateObj.whatsapp_agen = payload.whatsapp_agen }
      if (payload.alamat_agen) { updateObj.alamat_agen = payload.alamat_agen }
      if (payload.ktp_agen) { updateObj.ktp_agen = payload.ktp_agen }
      if (payload.kode_agen) { updateObj.kode_agen = payload.kode_agen }
      if (payload.bos_agen) { updateObj.bos_agen = payload.bos_agen }

      firebase.database().ref('agen_db').child(payload.id).update(updateObj)
        .then(() => {
          commit('setLoading', false)
          commit('updateAgen', payload)
        })
        .catch(error => {
          console.log(error)
          commit('setLoading', false)
        })
    },
    deleteAgenData ({commit}, payload) {
      commit('setLoading', true)
      const updateObj = {}
      updateObj.nama_agen = null
      updateObj.email_agen = null
      updateObj.telepon_agen = null
      updateObj.whatsapp_agen = null
      updateObj.alamat_agen = null
      updateObj.ktp_agen = null
      updateObj.kode_agen = null
      updateObj.bos_agen = null
      updateObj.creatorId = null

      firebase.database().ref('agen_db').child(payload.id).update(updateObj)
        .then(() => {
          commit('setLoading', false)
          commit('deleteAgen', payload)
        })
        .catch(error => {
          console.log(error)
          commit('setLoading', false)
        })
    }
  },
  getters: {
    loadedAgen_db (state) {
      return state.loadedAgen_db.sort((agenA, agenB) => {
        return agenA.id > agenB.id
      })
    },
    featuredAgen_db(state, getters){
      return getters.loadedAgen_db.slice(0, 5)
    },
    loadedAgen (state) {
      return (agenId) => {
        return state.loadedAgen_db.find((agen) => {
          return agen.id === agenId
        })
      }
    }
  }
}


