<template>
  <v-container fluid>
    <v-layout v-for="meetup in meetups" :key="meetup.id" class="mb-2">
      <v-flex xs10 offset-xs1 primary>
        <v-card class="info">
          <v-layout row>
            <v-flex xs5 sm4 md3>
              <v-card-media
                :src="meetup.imageUrl"
                height="100%"
              ></v-card-media>
            </v-flex>
            <v-flex xs7 sm8 md9>
              <v-card-title primary-title class="blue darken-3">
                <div>
                  <h3 class="white--text" mb-0>{{ meetup.title }}</h3>
                  <div>{{ meetup.date | date }}, {{ meetup.time }}</div>
                </div>
              </v-card-title>
              <v-card-actions class="blue darken-1">
                <v-spacer></v-spacer>
                <v-btn flat :to="'/meetups/' + meetup.id">
                  <v-icon left light>arrow_forward</v-icon>
                  View Meetup
                </v-btn>
              </v-card-actions>
            </v-flex>
          </v-layout>
      </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
  export default {
    computed: {
      meetups () {
        return this.$store.getters.loadedMeetups
      }
    }
  }
</script>


