import * as firebase from 'firebase'

export default {
  state: {
    loadedCustomer_db: [

    ]
  },
  mutations: {
    setLoadedCustomer_db (state, payload) {
      state.loadedCustomer_db = payload
    },
    createCustomer (state, payload) {
      state.loadedCustomer_db.push(payload)
    },
    updateCustomer (state, payload) {
      const customer = state.loadedCustomer_db.find(customer => {
        return customer.id === payload.id
      })
      if (payload.nama_customer) { customer.nama_customer = payload.nama_customer }
      if (payload.email_customer) { customer.email_customer = payload.email_customer }
      if (payload.telepon_customer) { customer.telepon_customer = payload.telepon_customer }
      if (payload.whatsapp_customer) { customer.whatsapp_customer = payload.whatsapp_customer }
      if (payload.alamat_customer) { customer.alamat_customer = payload.alamat_customer }
    },
    deleteCustomer (state, payload) {
      const customer = state.loadedCustomer_db.find(customer => {
        return customer.id === payload.id
      })
      if (payload.nama_customer) { customer.nama_customer = null }
      if (payload.email_customer) { customer.email_customer = null }
      if (payload.telepon_customer) { customer.telepon_customer = null }
      if (payload.whatsapp_customer) { customer.whatsapp_customer = null }
      if (payload.alamat_customer) { customer.alamat_customer = null }
    }
  },
  actions: {
    loadCustomer_db ({commit}) {
      commit('setLoading', true)
      firebase.database().ref('customer_db').once('value')
      .then((data) => {
        const customer_db = []
        const obj = data.val()
        for (let key in obj) {
          customer_db.push({
            id: key,
            nama_customer: obj[key].nama_customer,
            email_customer: obj[key].email_customer,
            telepon_customer: obj[key].telepon_customer,
            whatsapp_customer: obj[key].whatsapp_customer,
            alamat_customer: obj[key].alamat_customer
          })
        }
        //console.log(data)
        commit('setLoadedCustomer_db', customer_db)
        commit('setLoading', false)
      })
      .catch(
        (error) => {
          console.log(error)
          commit('setLoading', false)
        }
      )
    },
    createCustomer ({commit, getters}, payload) {
      const customer = {
        nama_customer: payload.nama_customer,
        email_customer: payload.email_customer,
        telepon_customer: payload.telepon_customer,
        whatsapp_customer: payload.whatsapp_customer,
        alamat_customer: payload.alamat_customer
      }
      let imageUrl
      let key
      firebase.database().ref('customer_db').push(customer)
      .then((data) => {
        key = data.key
        return key
      })
      .then(key => {
        const filename = payload.image.name
        const ext = filename.slice(filename.lastIndexOf('.'))
        return firebase.storage().ref('customer_db/' + key + '.' + ext).put(payload.image)
      })
      .then(fileData => {
        imageUrl = fileData.metadata.downloadURLs[0]
        return firebase.database().ref('customer_db').child(key).update({imageUrl: imageUrl})
      })
      .then(() => {
        commit('createCustomer', {
          ...customer,
          imageUrl: imageUrl,
          id: key
        })
      })
      .catch((error) => {
        console.log(error)
      })
      // Reach out to firebase and store it
    },
    updateCustomerData ({commit}, payload) {
      commit('setLoading', true)
      const updateObj = {}
      if (payload.nama_customer) { updateObj.nama_customer = payload.nama_customer }
      if (payload.email_customer) { updateObj.email_customer = payload.email_customer }
      if (payload.telepon_customer) { updateObj.telepon_customer = payload.telepon_customer }
      if (payload.whatsapp_customer) { updateObj.whatsapp_customer = payload.whatsapp_customer }
      if (payload.alamat_customer) { updateObj.alamat_customer = payload.alamat_customer }

      firebase.database().ref('customer_db').child(payload.id).update(updateObj)
        .then(() => {
          commit('setLoading', false)
          commit('updateCustomer', payload)
        })
        .catch(error => {
          console.log(error)
          commit('setLoading', false)
        })
    },
    deleteCustomerData ({commit}, payload) {
      commit('setLoading', true)
      const updateObj = {}
      updateObj.nama_customer = null
      updateObj.email_customer = null
      updateObj.telepon_customer = null
      updateObj.whatsapp_customer = null
      updateObj.alamat_customer = null

      firebase.database().ref('customer_db').child(payload.id).update(updateObj)
        .then(() => {
          commit('setLoading', false)
          commit('deleteCustomer', payload)
        })
        .catch(error => {
          console.log(error)
          commit('setLoading', false)
        })
    }
  },
  getters: {
    loadedCustomer_db (state) {
      return state.loadedCustomer_db.sort((customerA, customerB) => {
        return customerA.id > customerB.id
      })
    },
    featuredCustomer_db(state, getters){
      return getters.loadedCustomer_db.slice(0, 5)
    },
    loadedCustomer (state) {
      return (customerId) => {
        return state.loadedcustomer_db.find((customer) => {
          return customer.id === customerId
        })
      }
    }
  }
}


