<template>
  <v-card>
    <v-card-title>
      Data Agen
      <v-spacer></v-spacer>
      <v-text-field
        append-icon="search"
        label="Search"
        single-line
        hide-details
        v-model="search"
      ></v-text-field>
    </v-card-title>
    <v-data-table
        v-bind:headers="headers"
        v-bind:items="items"
        v-bind:search="search"
      >
      <template slot="items" slot-scope="props">
        <td>
          <v-edit-dialog
            lazy
          > {{ props.item.nama_agen }}
            <v-text-field
              slot="input"
              label="Edit"
              v-model="props.item.nama_agen"
              single-line
              counter
              :rules="[max25chars]"
            ></v-text-field>
          </v-edit-dialog>
        </td>
        <td class="text-xs-right">{{ props.item.email_agen }}</td>
        <td class="text-xs-right">{{ props.item.telepon_agen }}</td>
        <td class="text-xs-right">{{ props.item.whatsapp_agen }}</td>
        <td class="text-xs-right">{{ props.item.alamat_agen }}</td>
        <td class="text-xs-right">{{ props.item.ktp_agen }}</td>
        <td class="text-xs-right">{{ props.item.kode_agen }}</td>
        <td class="text-xs-right">
          <v-edit-dialog
            @open="tmp = props.item.bos_agen"
            @save="props.item.bos_agen = tmp || props.item.bos_agen"
            large
            lazy
          >
            <div>{{ props.item.bos_agen }}</div>
            <div slot="input" class="mt-3 title">Update bos_agen</div>
            <v-text-field
              slot="input"
              label="Edit"
              v-model="tmp"
              single-line
              counter
              autofocus
              :rules="[max25chars]"
            ></v-text-field>
          </v-edit-dialog>
        </td>
      </template>
      <template slot="pageText" slot-scope="{ pageStart, pageStop }">
        From {{ pageStart }} to {{ pageStop }}
      </template>
    </v-data-table>
  </v-card>
</template>

<script>
  export default {
    data () {
      return {
        max25chars: (v) => v.length <= 25 || 'Input too long!',
        tmp: '',
        search: '',
        pagination: {},
        headers: [
          {
            text: 'nama_agen',
            align: 'left',
            sortable: false,
            value: 'nama_agen'
          },
          { text: 'email_agen', value: 'email_agen' },
          { text: 'telepon_agen', value: 'telepon_agen' },
          { text: 'whatsapp_agen', value: 'whatsapp_agen' },
          { text: 'alamat_agen', value: 'alamat_agen' },
          { text: 'ktp_agen', value: 'ktp_agen' },
          { text: 'kode_agen', value: 'kode_agen' },
          { text: 'bos_agen', value: 'bos_agen' }
        ],
        items: [
          {
            value: false,
            nama_agen: 'Andi Chairil Furqan',
            email_agen: 'chairil@alifagency.com',
            telepon_agen: '628915656565',
            whatsapp_agen: '628915656565',
            alamat_agen: 'Depok',
            ktp_agen: '75489123654879',
            kode_agen: '1',
            bos_agen: '1'
          },
          {
            value: false,
            nama_agen: 'Muhammad Yahya',
            email_agen: 'yahya@alifagency.com',
            telepon_agen: '628915656567',
            whatsapp_agen: '628915656567',
            alamat_agen: 'Langgar',
            ktp_agen: '75489123654875',
            kode_agen: '2',
            bos_agen: '2'
          },
          {
            value: false,
            nama_agen: 'Abdul Rahman',
            email_agen: 'omeng@alifagency.com',
            telepon_agen: '628915656568',
            whatsapp_agen: '628915656568',
            alamat_agen: 'Bogor',
            ktp_agen: '75489123654876',
            kode_agen: '3',
            bos_agen: '3'
          }
          
        ]
      }
    }
  }
</script>


