<template>
  <v-app id="inspire">

    <!-- navigation samping -->
    <v-navigation-drawer
      dark
      clipped
      temporary
      fixed
      v-model="sideNav"
      app
    >
      <v-list dense>
        <v-list-tile @click="" to="/" tag="span" style="cursor: pointer">
          <v-list-tile-action>
            <v-icon>home</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>
                <router-link to="/" tag="span" style="cursor: pointer">Home</router-link>
            </v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>

        <template v-for="(item, i) in menuItems">
          <v-layout
            row
            align-center
            v-if="item.menuItems"
            :key="i"
          >
            <v-flex xs6>
              <v-subheader v-if="item.heading">
                {{ item.heading }}
              </v-subheader>
            </v-flex>
            <v-flex xs6 class="text-xs-center">
              <a href="#!" class="body-2 black--text">EDIT</a>
            </v-flex>
          </v-layout>
          <v-list-group v-else-if="item.children" v-model="item.model" no-action>
            <v-list-tile slot="item" @click="" :to="item.link"
            >
              <v-list-tile-action>
                <v-icon>{{ item.model ? item.icon : item['icon-alt'] }}</v-icon>
              </v-list-tile-action>
              <v-list-tile-content>
                <v-list-tile-title>
                  {{ item.title }}
                </v-list-tile-title>
              </v-list-tile-content>
            </v-list-tile>

            <v-list-tile
              v-for="(child, i) in item.children"
              :key="i"  @click="" :to="child.link"
            >
              <v-list-tile-action v-if="child.icon">
                <v-icon>{{ child.icon }}</v-icon>
              </v-list-tile-action>
              <v-list-tile-content>
                <v-list-tile-title>
                  {{ child.title }}
                </v-list-tile-title>
              </v-list-tile-content>
            </v-list-tile>
          </v-list-group>
          <v-list-tile v-else @click="" :to="item.link">
            <v-list-tile-action>
              <v-icon>{{ item.icon }}</v-icon>
            </v-list-tile-action>
            <v-list-tile-content>
              <v-list-tile-title>
                {{ item.title }}
              </v-list-tile-title>
            </v-list-tile-content>
          </v-list-tile>
        </template>

        <v-list-tile v-if="userIsAuthenticated" @click="onLogout">
          <v-list-tile-action>
            <v-icon>exit_to_app</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>Logout</v-list-tile-content>

        </v-list-tile>
  

      </v-list>
    </v-navigation-drawer>
    <!-- navigation samping -->


    <!-- navigation atas -->
    <v-toolbar color="primary" dark fixed app>
      <v-toolbar-side-icon @click.stop="sideNav = !sideNav"></v-toolbar-side-icon>
      <v-toolbar-title>
        <router-link to="/" tag="span" style="cursor: pointer">Alif Agency</router-link>
      </v-toolbar-title>
      <v-spacer></v-spacer>

      <v-toolbar-items class="hidden-xs-only">

        <v-btn v-if="userIsAuthenticated" flat to="/agen">
          <v-icon left>person</v-icon>
          Agen
        </v-btn>
        <v-btn v-if="userIsAuthenticated" flat to="/customer">
          <v-icon left>face</v-icon>
          Customer
        </v-btn>
        
        <v-menu open-on-hover offset-y>
          <v-btn flat slot="activator">
            <v-icon left>place</v-icon>Lokasi Lahan
          </v-btn>
          <v-list>
            <v-list-tile title="banten" to="/banten">
              <v-icon left>landscape</v-icon>Banten
            </v-list-tile>
            <v-list-tile icon="supervisor_account" title="cipanas" to="/cipanas">
              <v-icon left>landscape</v-icon>Cipanas
            </v-list-tile>
            <v-list-tile icon="supervisor_account" title="cirebon" to="/cirebon">
              <v-icon left>landscape</v-icon>Cirebon
            </v-list-tile>
            <v-list-tile icon="supervisor_account" title="jasinga" to="/jasinga">
              <v-icon left>landscape</v-icon>Jasinga
            </v-list-tile>
            <v-list-tile icon="supervisor_account" title="jonggol" to="/jonggol">
              <v-icon left>landscape</v-icon>Jonggol
            </v-list-tile>
            <v-list-tile icon="supervisor_account" title="tanjungsari" to="/tanjungsari">
              <v-icon left>landscape</v-icon>Tanjungsari
            </v-list-tile>

          </v-list>
        </v-menu>

        <v-menu open-on-hover v-if="userIsAuthenticated" offset-y>
          <v-btn flat slot="activator">
            <v-icon left>supervisor_account</v-icon>
            Meetups
          </v-btn>
          <v-list>
            <v-list-tile icon="supervisor_account" title="meetups" to="/meetups">
              <v-list-tile-title>View Meetups</v-list-tile-title>
            </v-list-tile>
            <v-list-tile icon="room" title="create_meetup" to="/meetup/new">
              <v-list-tile-title>Create Meetups</v-list-tile-title>
            </v-list-tile>
          </v-list>
        </v-menu>


        <v-menu open-on-hover v-if="userIsAuthenticated" offset-y>
          <v-btn flat slot="activator">
            <v-icon left>autorenew</v-icon>
            Test Test
          </v-btn>
          <v-list>
            
            <v-list-tile icon="send" title="grid" to="/grid">
              <v-list-tile-title>Page Grid</v-list-tile-title>
            </v-list-tile>
            <v-list-tile icon="send" title="datatable" to="/datatable">
              <v-list-tile-title>Data Table</v-list-tile-title>
            </v-list-tile>
            <v-list-tile icon="send" title="Link1" to="/link1">
              <v-list-tile-title>Link1</v-list-tile-title>
            </v-list-tile>
            <v-list-tile icon="send" title="Link2" to="/link2">
              <v-list-tile-title>Link2</v-list-tile-title>
            </v-list-tile>
            <v-list-tile icon="send" title="Link3" to="/link3">
              <v-list-tile-title>Link3</v-list-tile-title>
            </v-list-tile>
            <v-list-tile icon="send" title="Link4" to="/link4">
              <v-list-tile-title>Link4</v-list-tile-title>
            </v-list-tile>
            <v-list-tile icon="send" title="Link5" to="/link5">
              <v-list-tile-title>Link5</v-list-tile-title>
            </v-list-tile>


          </v-list>
        </v-menu>

        <!-- <v-btn v-if="!userIsAuthenticated" flat to="/signup">
          <v-icon left>face</v-icon>
          Sign Up
        </v-btn> -->
        <v-btn v-if="!userIsAuthenticated" flat to="/signin">
          <v-icon left>lock_open</v-icon>
          Sign In
        </v-btn>

        <v-btn v-if="userIsAuthenticated" flat @click="onLogout">
          <v-icon left>exit_to_app</v-icon>
          Logout
        </v-btn>
      </v-toolbar-items>
    </v-toolbar>

    <v-content>
      
      <router-view></router-view>

    </v-content>
    <v-footer color="primary" app>
        <v-spacer></v-spacer>
        <span class="white--text">Alif Agency &copy; 2017</span>
    </v-footer>
  </v-app>
</template>

<script>
/* eslint-disable */
export default {
    data () {
      return {
        sideNav: null
      }
    },
    computed: {
      menuItems () {
        let menuItems = [
          {
            icon: 'keyboard_arrow_down',
            'icon-alt': 'keyboard_arrow_down',
            title: 'LOKASI',
            model: false,
            children: [
              { icon: 'add', link: '/jonggol', title: 'Jonggol' },
              { icon: 'add', link: '/tanjungsari', title: 'Tanjungsari' },
              { icon: 'add', link: '/cipanas', title: 'Cipanas' },
              { icon: 'add', link: '/jasinga', title: 'Jasinga' },
              { icon: 'add', link: '/cirebon', title: 'Cirebon' },
              { icon: 'add', link: '/banten', title: 'Banten' }
            ]
          },
          { icon: 'lock_open', title: 'Sign In', link: '/signin' }
          //{ icon: 'face', title: 'Sign Up', link: '/signup' }

        ]
        if (this.userIsAuthenticated) {
          menuItems = [
            { icon: 'person', title: 'Agen', link: '/agen' },
            { icon: 'face', title: 'Customer', link: '/customer' },
            { icon: 'supervisor_account', title: 'Meetups', link: '/meetups' }
            //{ icon: 'person', title: 'Profile', link: '/profile' }
          ]
        }
        return menuItems
      },
      userIsAuthenticated () {
        return this.$store.getters.user !== null && this.$store.getters.user !== undefined
      }
    },
    methods: {
      onLogout () {
        this.$store.dispatch('logout')
      }
    },
    props: {
      source: String
    }
  }
</script>



