<template>
  <v-dialog width="350px" persistent v-model="editDialog">
    <v-chip color="green" text-color="white" accent slot="activator" small>
      Edit Date
    </v-chip>
    <v-card>
      <v-container>
        <v-layout row wrap>
          <v-flex xs12>
            <v-card-title>
              <h3 class="primary--text">Edit Date</h3>
            </v-card-title>
          </v-flex>
        </v-layout>

        <v-layout row wrap>
          <v-flex xs12>
            <v-date-picker v-model="editableDate" actions>
              <template slot-scope="{save, cancel}">
                <v-btn class="blue-text darken-1" @click="onSaveChanges">Save</v-btn>
                <v-btn class="blue-text darken-1" @click="editDialog = false">Close</v-btn>
              </template>
            </v-date-picker>
          </v-flex>
        </v-layout>
      </v-container>
    </v-card>
  </v-dialog>
</template>

<script>
  export default {
    props: ['meetup'],
    data () {
      return {
        editDialog: false,
        editableDate: null
      }
    },
    methods: {
      onSaveChanges () {
        //const newDate = new Date(this.meetup.date)
        //const newDay = new Date(this.editableDate).getUTCDate()
        //const newMonth = new Date(this.editableDate).getUTCMonth()
        //const newYear = new Date(this.editableDate).getUTCFullYear()
        //newDate.setUTCDate(newDay)
        //newDate.setUTCMonth(newMonth)
        //newDate.setUTCFullYear(newYear)

        //const newDate = this.meetup.date
        const newDay = this.editableDate
        this.$store.dispatch('updateMeetupData', {
          id: this.meetup.id,
          date: newDay
        })
      }
    },
    created () {
      this.editableDate = this.meetup.date
    }
  }
</script>


