<template>
  <v-card>
          <!-- "https://www.highreshdwallpapers.com/wp-content/uploads/2015/01/Glorious-Autumn-Sunshine-1920x1080.jpg"> -->

      <template>
        <v-parallax :src="require('@/components/img/kurma_muda.jpg')" height="300">
          <v-layout column align-center justify-center>
            <h2 class="white--text">DAFTAR CUSTOMER</h2>
            <h1 class="white--text">ALIF AGENCY</h1>
            <h3 class="white--text">Berbagi tanpa batas</h3>
            <h4 class="white--text">Tapi jangan sampai habis semua</h4>
          </v-layout>
        </v-parallax>
      </template>
    
    <v-card-title>
      <!-- <v-chip
        color="primary"
        text-color="white"
        @click.native.stop="dialog_add = !dialog_add"
      >
        <v-avatar>
          <v-icon>account_circle</v-icon>
        </v-avatar>
        Add
      </v-chip> -->
      <app-add-customer-dialog></app-add-customer-dialog>
      <router-view :key="$route.path"></router-view>

    
      <v-spacer></v-spacer>
      <v-text-field
        append-icon="search"
        label="Search"
        single-line
        hide-details
        top
        v-model="search"
      ></v-text-field>
    </v-card-title>

    <hr>

    <v-data-table
        v-bind:headers="headers"
        v-bind:items="customer"
        v-bind:search="search"
      >

      <template slot="items" slot-scope="props">
        <td>
          <v-edit-dialog lazy> {{ props.item.nama_customer }}
            <v-text-field
              slot="input"
              label="Edit"
              v-model="props.item.nama_customer"
              single-line
              counter
              :rules="[max25chars]"
            ></v-text-field>
          </v-edit-dialog>
        </td>
        <td class="text-xs-right">{{ props.item.email_customer }}</td>
        <td class="text-xs-right">{{ props.item.telepon_customer }}</td>
        <td class="text-xs-right">{{ props.item.whatsapp_customer }}</td>
        <td class="text-xs-right">{{ props.item.alamat_customer }}</td>

        <td>
          <!--
          <v-spacer></v-spacer>
          <v-btn fab dark small color="red"><v-icon>delete</v-icon></v-btn>
          <v-btn fab dark small color="green"><v-icon>edit</v-icon></v-btn>
          -->
          <v-spacer></v-spacer>
          <app-edit-customer-details-dialog :customer="props.item"></app-edit-customer-details-dialog>
          <router-view :key="$route.path"></router-view>
        </td>


      </template >

        <template slot="pageText" slot-scope="{ pageStart, pageStop }">
          From {{ pageStart }} to {{ pageStop }}
        </template>
    </v-data-table>

    <!-- <v-dialog v-model="dialog_add" max-width="500px">
      <v-card dark>
        <v-card-title class="headline">
          Penambahan Customer
        </v-card-title>
        <v-card-text>
          <v-text-field
            box dark
            label="Full Name"
            placeholder="Muh..."
            v-model="nama_customer"
            required
            id="nama_customer"
            :rules="[() => !!nama_customer || 'This field is required']"
            :error-messages="errorMessages"
          ></v-text-field>
          <v-text-field
            box dark
            label="Email Address"
            placeholder="customer@alifagency.com"
            v-model="email_customer"
            required
            id="email_customer"
            :rules="[() => !!email_customer || 'This field is required']"
            :error-messages="errorMessages"
          ></v-text-field>
          <v-text-field
            box dark
            label="Nomor Telepon"
            placeholder="628**********"
            v-model="telepon_customer"
            required
            id="telepon_customer"
            :rules="[() => !!telepon_customer || 'This field is required']"
            :error-messages="errorMessages"
          ></v-text-field>
          <v-text-field
            box dark
            label="Nomor Whatsapp"
            placeholder="628**********"
            v-model="whatsapp_customer"
            required
            id="whatsapp_customer"
            :rules="[() => !!whatsapp_customer || 'This field is required']"
            :error-messages="errorMessages"
          ></v-text-field>

          <v-text-field
            box dark
            label="Alamat Rumah"
            placeholder="Jl. Langgar 80 B, Kel. Kemiri Muka,Kec.Beji, Kota Depok, Prov. Jawa Barat"
            :rules="[
              () => !!alamat_customer || 'This field is required',
              () => !!alamat_customer && alamat_customer.length <= 219 || 'Address must be less than 219 characters',
              this.addressCheck
            ]"
            multi-line
            v-model="alamat_customer"
            id="alamat_customer"
            counter="219"
            required
          ></v-text-field>

        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>
          
          <v-btn small color="primary" flat @click.stop="dialog_add=false">Close</v-btn>
          <v-btn small color="primary" flat @click="onCreateCustomer">Submit</v-btn>
          <router-view :key="$route.path"></router-view>
        </v-card-actions>
      </v-card>

  </v-dialog> -->



  </v-card>
</template>

<script>
  export default {
    data () {
      return {
        dialog_add: false,
        max25chars: (v) => v.length <= 25 || 'Input too long!',
        tmp: '',
        search: '',
        pagination: {},
        headers: [
          {
            text: 'Nama',
            align: 'left',
            sortable: true,
            value: 'nama_customer'
          },
          { text: 'Email', value: 'email_customer' },
          { text: 'Telepon', value: 'telepon_customer' },
          { text: 'Whatsapp', value: 'whatsapp_customer' },
          { text: 'Alamat', value: 'alamat_customer' }
        ],
        errorMessages: [],

        nama_customer: null,
        email_customer: null,
        telepon_customer: null,
        whatsapp_customer: null,
        alamat_customer: null,
        formHasErrors: false
          
      }
    },
    created () {
      this.fetchData()
    },
    computed: {
      customer () {
        return this.$store.getters.loadedCustomer_db
      },
      form () {
        return {
          nama_customer: this.nama_customer,
          email_customer: this.email_customer,
          telepon_customer: this.telepon_customer,
          whatsapp_customer: this.whatsapp_customer,
          alamat_customer: this.alamat_customer

        }
      },
      userIsAuthenticated () {
        return this.$store.getters.user !== null && this.$store.getters.user !== undefined
      },
    },

    watch: {
      name () {
        this.errorMessages = []
      }
    },

    methods: {
      fetchData () {
       // fetch data
       
      },
      addressCheck () {
        this.errorMessages = this.alamat_customer && !this.nama_customer
          ? ['Hey! I\'m required']
          : []

        return true
      },
      resetForm () {
        this.errorMessages = []
        this.formHasErrors = false

        Object.keys(this.form).forEach(f => {
          this.$refs[f].reset()
        })
      },
      onCreateCustomer () {
        this.dialog_add = false
        this.formHasErrors = false

        Object.keys(this.form).forEach(f => {
          if (!this.form[f]) this.formHasErrors = true

          //this.$refs[f].validate(true)
        })
        if (this.nama_customer.trim() === '' || 
            this.email_customer.trim() === '' || 
            this.telepon_customer.trim() === '' ||
            this.whatsapp_customer.trim() === '' || 
            this.alamat_customer.trim() === ''
          ) {
          return
        }
        const customerData = {
          nama_customer: this.nama_customer,
          email_customer: this.email_customer,
          telepon_customer: this.telepon_customer,
          whatsapp_customer: this.whatsapp_customer,
          alamat_customer: this.alamat_customer
        }
        this.$store.dispatch('createCustomer', customerData)
        this.$router.push('/customer')

      }

    }
  }
</script>



