<template>
  <v-dialog max-width="500px" persistent v-model="dialog_add">
    <!-- <v-btn fab dark small color="red" accent slot="activator">
      <v-icon>edit</v-icon>
    </v-btn> -->
    <v-chip
      color="primary"
      text-color="white"
      accent slot="activator"
      class="subheading"
    >
      <v-avatar>
        <v-icon>assignment</v-icon>
      </v-avatar>
      Formulir Customer
    </v-chip>
    <v-card dark>
      <v-container>
        <v-layout row wrap>
          <v-flex xs12>
            <h3 class="primary--text">Formulir Data Customer</h3>
          </v-flex>
        </v-layout>
        <v-divider></v-divider>
        <v-layout row wrap>
          <v-flex xs12>
            <v-card-text>
              <v-text-field
                box dark
                label="Full Name"
                placeholder="Muh..."
                v-model="nama_customer"
                required
                id="nama_customer"
                :rules="[() => !!nama_customer || 'This field is required']"
                :error-messages="errorMessages"
              ></v-text-field>
              <v-text-field
                box dark
                label="Email Address"
                placeholder="customer@alifagency.com"
                v-model="email_customer"
                required
                id="email_customer"
                :rules="emailRules"
                :error-messages="errorMessages"
              ></v-text-field>
              <v-text-field
                box dark
                label="Nomor Telepon"
                placeholder="628**********"
                v-model="telepon_customer"
                required
                id="telepon_customer"
                :rules="[() => !!telepon_customer || 'This field is required']"
                :error-messages="errorMessages"
              ></v-text-field>
              <v-text-field
                box dark
                label="Nomor Whatsapp"
                placeholder="628**********"
                v-model="whatsapp_customer"
                required
                id="whatsapp_customer"
                :rules="[() => !!whatsapp_customer || 'This field is required']"
                :error-messages="errorMessages"
              ></v-text-field>
              <v-text-field
                box dark
                label="Alamat Rumah"
                placeholder="Jl. Langgar 80 B, Kel. Kemiri Muka, Kec. Beji, Kota Depok, Prov. Jawa Barat"
                :rules="[
                  () => !!alamat_customer || 'This field is required',
                  () => !!alamat_customer && alamat_customer.length <= 219 || 'Address must be less than 219 characters',
                  this.addressCheck
                ]"
                multi-line
                v-model="alamat_customer"
                id="alamat_customer"
                counter="219"
                required
              ></v-text-field>
            </v-card-text>

          </v-flex>
        </v-layout>
        <v-divider></v-divider>
        <v-layout row wrap>
          <v-flex xs12>
            <v-card-actions>
              <v-spacer></v-spacer>
              <router-view :key="$route.path"></router-view>
              <v-btn 
                class="blue--text darken-1" 
                @click="dialog_add = false"
              >Cancel</v-btn>
              <router-view :key="$route.path"></router-view>
              <v-btn 
                class="green--text darken-1" 
                @click="onCreateCustomer"
              >Submit</v-btn>
              
            </v-card-actions>
          </v-flex>
        </v-layout>
      </v-container>
    </v-card>
  </v-dialog>
</template>

<script>
  export default {
    data () {
      return {
        dialog_add: false,
        max25chars: (v) => v.length <= 25 || 'Input too long!',

        emailRules: [ //form isian data diri
          v => {
            return !!v || 'E-mail is required'
          },
          v => /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(v) || 'E-mail must be valid'
        ],

        headers: [
          {
            text: 'Nama',
            align: 'left',
            sortable: true,
            value: 'nama_customer'
          },
          { text: 'Email', value: 'email_customer' },
          { text: 'Telepon', value: 'telepon_customer' },
          { text: 'Whatsapp', value: 'whatsapp_customer' },
          { text: 'Alamat', value: 'alamat_customer' }
        ],

        errorMessages: [],

        nama_customer: null,
        email_customer: null,
        telepon_customer: null,
        whatsapp_customer: null,
        alamat_customer: null,
        formHasErrors: false,
      }
    },
    created () { //form isian customer
      this.fetchData()
    },
    watch: { //form isian customer
      name () {
        this.errorMessages = []
      }
    },
    computed: {
      customer () {
        return this.$store.getters.loadedCustomer_db
      },
      form () {
        return {
          nama_customer: this.nama_customer,
          email_customer: this.email_customer,
          telepon_customer: this.telepon_customer,
          whatsapp_customer: this.whatsapp_customer,
          alamat_customer: this.alamat_customer

        }
      },
      userIsAuthenticated () {
        return this.$store.getters.user !== null && this.$store.getters.user !== undefined
      },
    },
    methods: {
      fetchData () {
       // fetch data
       
      },
      addressCheck () {
        this.errorMessages = this.alamat_customer && !this.nama_customer
          ? ['Hey! I\'m required']
          : []

        return true
      },
      resetForm () {
        this.errorMessages = []
        this.formHasErrors = false

        Object.keys(this.form).forEach(f => {
          this.$refs[f].reset()
        })
      },
      onCreateCustomer () {
        this.dialog_add = false
        this.formHasErrors = false

        Object.keys(this.form).forEach(f => {
          if (!this.form[f]) this.formHasErrors = true

          //this.$refs[f].validate(true)
        })
        if (this.nama_customer.trim() === '' || 
            this.email_customer.trim() === '' || 
            this.telepon_customer.trim() === '' ||
            this.whatsapp_customer.trim() === '' || 
            this.alamat_customer.trim() === ''
          ) {
          return
        }
        const customerData = {
          nama_customer: this.nama_customer,
          email_customer: this.email_customer,
          telepon_customer: this.telepon_customer,
          whatsapp_customer: this.whatsapp_customer,
          alamat_customer: this.alamat_customer
        }
        this.$store.dispatch('createCustomer', customerData)
        this.$router.push('/customer')

      }

    }
  
  }
</script>


