<template>
  <v-layout>
    <v-container>

    <div>
        <br>
        <br>
        <p>Cookie current = {{ my_cookie }}</p>
        <p>cookie_data_agen = {{ this.$cookies.get("cookie_data_agen") }}</p>
        <p>cookie_data_agen1 = {{ this.$cookies.get("cookie_data_agen1") }}</p>
        <p>URL = {{ this.$route.path }}</p>
        <br>
    </div>

    <div>

    </div>

  </v-container>
  </v-layout>
</template>
    
<script>
  export default {
      data: () => ({
      lorem: 'Lorem ipsum dolor sit amet, mel at clita quando. Te sit oratio vituperatoribus, nam ad ipsum posidonium mediocritatem, explicari dissentiunt cu mea. Repudiare disputationi vim in, mollis iriure nec cu, alienum argumentum ius ad. Pri eu justo aeque torquatos.'
    }),
    computed: {
      my_cookie () {
        // check a cookie exist
        if (this.$cookies.isKey("cookie_data_agen")==true) {
          this.$cookies.remove("cookie_data_agen")
        }

        if (this.$cookies.isKey("cookie_data_agen2")==false) {
          //https://www.npmjs.com/package/vue-cookies
          var mypath= this.$route.path
          this.$cookies.set("cookie_data_agen2",mypath, 60 * 60 * 24 * 30) // 1 month after, expire
          return this.$cookies.get("cookie_data_agen2")
          //return this.$cookies.isKey("cookie_data_agen")
          //return this.$route.path
          //return mypath
        } else { return this.$cookies.get("cookie_data_agen2") }
/*
// set path
this.$cookies.set("use_path_argument","value","1d","/app");  

// set domain
this.$cookies.set("use_path_argument","value",null, null, "domain.com");   // default 1 day after,expire

// set secure
this.$cookies.set("use_path_argument","value",null, null, null,true);

// check a cookie exist
this.$cookies.isKey("token")

// get a cookie
this.$cookies.get("token")

// remove a cookie
this.$cookies.remove("token")

// get all cookie key names, line shows
this.$cookies.keys().join("\n")

// vue-cookies global
[this | Vue | window].$cookies.[method] 

*/



      }
    },
    methods: {
      onClickButton () {
        return window.location.href = 'https://vuejs.org/v2/guide/syntax.html'
        
        //if (/*check some conditions*/) {
        //  location.href = 'https://external.com/'
        //} else {
        //  this.$router.go({....})
        //}
      }
    }
  }

  
</script>


